<template>
  <section
    class="message-section"
    id="contact"
    data-spy="scroll"
    data-target="#mainNav"
    data-offset="100"
  >
    <div class="container">
      <div class="row">
        <div class="col-md-10 col-lg-8 mx-auto text-center" id="message-area">
          <i class="far fa-paper-plane fa-2x mb-2 text-white"></i>
          <h2 class="text-white mb-4">Message & Contact</h2>
          <form class="form-group">
            <input
              class="form-control flex-fill mr-0 mr-sm-2 mb-3 mb-sm-0"
              id="inputName"
              type="name"
              placeholder="Name"
              v-model="name"
            />
            <input
              class="form-control flex-fill mt-3 mr-0 mr-sm-2 mb-3 mb-sm-0"
              id="inputEmail"
              type="email"
              placeholder="Email"
              v-model="email"
            />
            <textarea
              class="form-control flex-fill mt-3 mr-0 mr-sm-2 mb-3 mb-sm-0"
              id="inputNote"
              rows="6"
              type="text"
              placeholder="Message"
              v-model="note"
            ></textarea>
            <button
              type="button"
              class="btn btn-lg mt-3"
              id="submit-btn"
              @click="submitMessage"
            >SUBMIT</button>
          </form>
        </div>
      </div>
      <b-modal ref="alert-modal" hide-header-close>
        <template v-slot:modal-header>
          <h5 class="mx-auto" style="font-size: 24px">ERROR!</h5>
        </template>
        <p class="text-center" style="font-size: 18px">There is nothing to submit...</p>
        <template v-slot:modal-footer>
          <b-button type="button" class="btn mx-auto" @click="hideModal">Close</b-button>
        </template>
      </b-modal>
    </div>
  </section>
</template>
  
<script>
import Axios from "axios";
export default {
  name: "ContactForm",
  data: function () {
    return {
      name: "",
      email: "",
      note: "",
    };
  },
  methods: {
    submitMessage: function () {
      // console.log("click");
      if (!this.name && !this.email && !this.note) {
        this.$refs["alert-modal"].show();
      } else {
        Axios.post("api/newmessage", {
          name: this.name,
          email: this.email,
          note: this.note,
        })
          .then((res) => {
            console.log(res);
            this.name = "";
            this.email = "";
            this.note = "";
          })
          .catch((err) => console.log(err));
      }
    },
    hideModal: function () {
      this.$refs["alert-modal"].hide();
    },
  },
};
</script>

